/*
--- 01 TYPOGRAPHY SYSTEM

- Font sizes (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128
*/
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 18px;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.4;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 1440px;
  margin-inline: auto;
  padding: 10px;
}
@media (max-width: 1440px) {
  .container {
    max-width: 1100px;
    margin-inline: auto;
    padding: 0 30px;
  }
}
@media (max-width: 1100px) {
  .container {
    max-width: 1000px;
  }
}
@media (max-width: 960px) {
  .container {
    max-width: 768px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 600px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  color: hsl(192deg, 100%, 9%);
}

.home__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  height: 160px;
}

@media (max-width: 768px) {
  .home__logo {
    width: 50%;
  }
}

.footer {
  background-color: hsl(192deg, 100%, 9%);
}

.footer__content {
  padding: 150px 0 60px 0;
}

.footer__boxes {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 1fr;
  justify-content: space-between;
  color: #fff;
  gap: 35px;
}
@media (max-width: 960px) {
  .footer__boxes {
    grid-template-columns: 1fr;
    padding: 10px 30px;
    gap: 0;
  }
}

@media (max-width: 1200px) {
  .footer__box:nth-child(1) {
    margin-bottom: 50px;
  }
}

.footer__box:nth-child(2) {
  display: flex;
  justify-content: center;
}
@media (max-width: 1200px) {
  .footer__box:nth-child(2) {
    justify-content: flex-start;
  }
}

@media (max-width: 1200px) {
  .footer__box:nth-child(3) {
    margin-bottom: 50px;
  }
}

.footer__box:nth-child(4) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1200px) {
  .footer__box:nth-child(4) {
    align-items: center;
  }
}

.footer__logo {
  margin-left: 5px;
  margin-bottom: 35px;
}
@media (max-width: 1440px) {
  .footer__logo {
    padding: 0 15px;
  }
}

.footer__location {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 20px;
}

.footer__phone {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.footer__email {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.footer__icon {
  margin-right: 20px;
}

.footer__item {
  margin-bottom: 22px;
}

.footer__list {
  list-style: none;
}

.footer__link {
  color: #fff;
  text-decoration: none;
}

.footer__link:hover {
  text-decoration: underline;
}

.follow--list {
  display: flex;
}

.footer__icon--border {
  border: white;
  width: 30px;
  height: 30px;
}

.footer__icon--icon {
  padding: 10px;
  width: 24px;
  height: 24px;
  color: #fff;
  cursor: pointer;
}

@media (max-width: 1200px) {
  .footer__follow {
    margin-bottom: 30px;
  }
}

.footer__copyrights {
  font-size: 14px;
}

.icon--border:hover {
  border: 2px solid hsl(322deg, 100%, 66%);
  color: hsl(322deg, 100%, 66%);
}

.footer__icon--icon:not(:last-child) {
  margin-right: 15px;
}

.icon--border {
  border: 2px solid #fff;
  border-radius: 50%;
}

.btn {
  display: inline-block;
  padding: 20px 75px;
  background-color: hsl(322deg, 100%, 66%);
  border-radius: 50px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  border: none;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  transition: 0.3s;
}

.btn:hover,
.btn:active {
  opacity: 0.5;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.home__button {
  display: inline-block;
  padding: 15px 65px;
  font-weight: 700;
  background-color: #fff;
  color: hsl(192deg, 100%, 9%);
  border-radius: 50px;
  cursor: pointer;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  transition: 0.3s;
}
@media (max-width: 768px) {
  .home__button {
    padding: 15px 50px;
  }
}
@media (max-width: 600px) {
  .home__button {
    padding: 15px 40px;
  }
}

.home__button:hover,
.home__button:active {
  color: hsl(208deg, 11%, 55%);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.margin-left {
  margin-left: 80px;
}

.home {
  margin-bottom: 150px;
  background-color: hsl(193deg, 100%, 96%);
}

.home__boxes {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  justify-content: center;
  align-items: center;
  padding: 30px 0 60px 0;
  gap: 50px;
}
@media (max-width: 1440px) {
  .home__boxes {
    grid-template-columns: 1fr;
    justify-content: center;
    text-align: center;
  }
}

.home__heading {
  font-size: 52px;
  margin-bottom: 45px;
}
@media (max-width: 600px) {
  .home__heading {
    font-size: 44px;
  }
}

.home__paragraph {
  line-height: 1.5;
  margin-bottom: 50px;
}

.about {
  position: relative;
}

.about__box {
  display: grid;
  grid-template-columns: 40% 40%;
  justify-content: space-between;
  align-items: center;
  padding: 50px 25px 50px 125px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 15px;
  margin-bottom: 50px;
}
@media (max-width: 1200px) {
  .about__box {
    grid-template-columns: 1fr;
    align-items: center;
    justify-content: center;
    padding: 50px;
    gap: 50px;
  }
}
@media (max-width: 375px) {
  .about__box {
    padding: 30px 15px;
  }
}

@media (max-width: 1200px) {
  .about__img {
    order: -1;
    margin-bottom: 30px;
  }
}

.about__box:nth-child(3) {
  margin-bottom: 250px;
}
@media (max-width: 375px) {
  .about__box:nth-child(3) {
    margin-bottom: 200px;
  }
}

@media (max-width: 1200px) {
  .about__title {
    text-align: center;
  }
}

.about__heading {
  margin-bottom: 30px;
  font-size: 30px;
}
@media (max-width: 960px) {
  .about__heading {
    font-size: 22px;
  }
}
@media (max-width: 375px) {
  .about__heading {
    font-size: 20px;
  }
}

.about_paragraph {
  color: hsl(208deg, 11%, 55%);
  line-height: 1.6;
}

.about__overlay {
  position: absolute;
  background-color: #fff;
  width: 55%;
  text-align: center;
  padding: 40px 0;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.24);
  border-radius: 15px;
  left: 22.5%;
  bottom: -50px;
  z-index: 1;
}
@media (max-width: 1440px) {
  .about__overlay {
    bottom: -300px;
  }
}
@media (max-width: 1100px) {
  .about__overlay {
    width: 80%;
    left: 10%;
  }
}
@media (max-width: 960px) {
  .about__overlay {
    width: 75%;
    left: 12.5%;
  }
}
@media (max-width: 375px) {
  .about__overlay {
    width: 90%;
    left: 5%;
    bottom: -250px;
    padding: 10px;
  }
}

.about__button {
  padding: 20px 100px;
  font-size: 20px;
}
@media (max-width: 1100px) {
  .about__button {
    padding: 18px 80px;
    font-size: 18px;
  }
}
@media (max-width: 960px) {
  .about__button {
    padding: 16px 60px;
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  .about__button {
    padding: 15px 40px;
    font-size: 14px;
  }
}